
import Mytab from './Mytab';
import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import Login from './login'
const Shoucang = (props) => {
    // console.log(props.shoucanglist)
    return (
        <div>
           
            {
                props.shoucanglist.map((goods,i)=>(
                           
                    <div >
                        <img src={goods.src} style={{width:'40%',height:'100px',margin:'0'}}/>
                        <div>
                            <span>{goods.title}</span>
                            <span style={{color:'red'}}>￥{goods.price}</span>
                        </div>
                        <button  
                        style={{width:'40%',height:'30px',margin:'0'}}
                        onClick={()=>{
                    
                            props.dispatch({
                                type:'DELETE',
                                index:i
                            })
                   
                        }}>删除</button>
                    </div>
                            
                ))
            }
            <Mytab/>

        </div>

    )

}
const mapStateToProps = (state) => ({
    shoucanglist:state.shoucanglist,
    // username: state.log.username,

})
export default connect(mapStateToProps)(Shoucang)