import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-shouye1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shouye'></i>                           
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>                           
                        }
                        title="收藏"
                        key="shoucang"
                        selected={ pathname === '/shoucang' }
                        onPress={ () => {
                            this.props.username?
                            this.props.history.push('/shoucang'):
                            this.props.history.push('/login')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan1'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        title="订单列表"
                        key="list"
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')
                        } }
                        data-seed="logId2"
                    >
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wode'></i>
                         }
                        selectedIcon={ 
                            <i className='iconfont icon-wodedangxuan'></i>
                        }
                        title="我的淘宝"
                        key="my"
            
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        data-seed="logId3"
                    >
                        
			</TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
const mapStateToProps = (state)=>({
    username:state.log.username
})
export default connect(mapStateToProps)(withRouter(Mytab));