import React,{Component}from 'react';
import './home.css';
import Mytab from './Mytab';
import {
    NavLink,Route, Redirect, Router,

} from 'react-router-dom'
import { SearchBar,Carousel } from 'antd-mobile'
import {goodlist} from './home/goodlist'
import {connect} from 'react-redux'
// import { render } from '@testing-library/react';
// import { Component } from 'react';
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
class Home extends React.Component{
    // constructor(props)  {
    //     super(props);
    // }
    state = {
        data: ['https://gw.alicdn.com/imgextra/i4/116/O1CN01fDEa3I1CjAZeUYZDB_!!116-0-lubanu.jpg', 'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 'https://gw.alicdn.com/imgextra/i2/104/O1CN010jvtis1Cdfp5OSQOA_!!104-0-lubanu.jpg'],
        imgHeight: 176,
    }
    componentDidMount(){
        // setTimeout(() => {
        //     this.setState({
        //         data: ['https://gw.alicdn.com/imgextra/i4/116/O1CN01fDEa3I1CjAZeUYZDB_!!116-0-lubanu.jpg', 'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 'https://gw.alicdn.com/imgextra/i2/104/O1CN010jvtis1Cdfp5OSQOA_!!104-0-lubanu.jpg']
        //     });
        // },100);
        this.props.dispatch(goodlist());
    }
    render(){
    return (
        <div className='home'>
            <header className='theTop'>
                <NavLink to='/searchbar'>
                <div className='searchbar'>
                    <a className='search'>
                        <span>
                            寻找宝贝店铺
                        </span>
                        
                    </a>
                </div>
                </NavLink>
            </header>
            <div className='Carousel'>
                <Carousel className="space-carousel"
                    autoplay
                    infinite
                >

                    {this.state.data.map((val, index) => (
                        <a
                            key={val}
                            style={{
                                display: 'block',
                                position: 'relative',
                                height: this.state.imgHeight
                            }}
                        >
                            <img
                                src={`${val}`}
                                alt=""
                                style={{ width: '100%',height:'120px'}}
                                onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                ))}
                </Carousel>
            </div>
            <div className='goods'>
                
                    {
                        this.props.goodlist.map((data) =>{
                            return(
                            <div
                                style={{
                                        height: '260px',
                                        position: 'relative',
                                        width: '170px',
                                        boxSizing:'border-box',
                                        display: '-webkit-flex',
                                        display: 'flex',
                                        WebkitBoxOrient: 'vertical',
                                        WebkitBoxDirection: 'normal',
                                        WebkitFlexDirection: 'column',
                                        flexDirection: 'column',
                                        alignContent: 'flex-start',
                                        flexShrink:'0',
                                        backgroundColor: '#fff',
                                        marginTop: '9px',
                                        borderRadius: '6px',
                                        overflow: 'hidden',
                                        marginLeft: '4.5px',
                                        marginRight: '4.5px',
                                }}
                                // selected={ xpathname === '/goods' }
                                onClick={()=>this.props.history.push({
                                    pathname:'/goods',
                                    src:data.img,
                                    title:data.title,
                                    price:data.price
                                })}
                                >
                                <div className='image'>
                                    <img className='pic'
                                    src={data.img}/>
                                </div>
                                <div className='title'>
                                    <div className='txt'>
                                        <span>{data.title}</span>
                                    </div>
                                    <div className='money'>
                                        <span className='money1'> ￥</span>
                                        <span className='money2'>{data.price}</span>
                                    </div>
                                </div>
                            </div> 
                        )})
                    }
            </div>
            <div className='footer'>
                <div className='bottom'>
                    <a>登录</a>
                    <a>注册</a>
                    <a>手淘App</a>
                    <a>用户反馈</a>
                </div>
            </div>
            <Mytab />
        </div>
        
    )
        }   
        // routerTo(goods) {
        //     this.props.history.push({pathname: `/goods/${goods.id}`, state: {data: goods}})
        //   }  
}
const mapStateToProps = (state) =>({
    goodlist:state.Goodredux.goodlist
})
export default connect(mapStateToProps)(Home)