let shoucang = [];
function shoucanglist(state=shoucang,action){
    if(action.type=='ADD'){
        //不可变对象
        // console.log(action)
        return [...state,action.shoucanglist]
    }
    if(action.type=='DELETE'){
        // console.log(action.index)
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}
export default shoucanglist;