import React,{Component}from 'react';
import {
    NavLink, Route, Redirect,
    useHistory,
    useLocation,
    useParams,
    useRouteMatch,
    withRouter
} from 'react-router-dom'
import { SearchBar ,NavBar,Icon, Button} from 'antd-mobile'
const Searchbar =(props)=>{
  const history = useHistory();
    return(
        <span 
          style={{display:'-webkit-flex'}}
        >
             <SearchBar
             style={{width:'80%',height:'30px', backgroundColor:'transparent',border:'0'}}
             />
             <span
                style={{
                  width:'20%',
                  height:'30px',
                  backgroundColor:'transparent',
                  lineHeight:'30px',
                  fontSize:'20px',
                  textAlign:'center'
              }}
                onClick={ () => props.history.goBack() }
            >取消</span>
        </span>
    )
}
export default Searchbar